@import url(https://fonts.googleapis.com/css2?family=B612:wght@400;700&display=swap);
.header {
  width: 100vw;
  height: 80px;
  background-color: var(--primary-back-color);
  display: flex;
  justify-content: center;
}

.header-wrapper {
  width: 90%;
  height: 100%;
  max-width: 1500px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  color: var(--primary-font-color);
  font-size: 30px;
}

.search {
  position: relative;
  height: 40px;
}

.search .input {
  background-color: rgb(179, 179, 179);
  border: 1px solid var(--primary-font-color);
  border-radius: 5px;
  font-size: 20px;
  padding: 10px;
  height: 40px;
  width: 40px;
  transition: width 0.3s ease;
}

.input {
  background-color: black;
}

.btn {
  background-color: rgb(179, 179, 179);
  border: 1px solid var(--primary-font-color);
  border-radius: 5px;
  cursor: pointer;
  font-size: 20px;
  position: absolute;
  top: 0;
  right: 0;
  height: 40px;
  width: 40px;
  -webkit-transform: translate(0, 0%);
          transform: translate(0, 0%);
  color: var(--primary-font-color);
  transition: all 0.4s cubic-bezier(1, -0.05, 1, -0.01);
}

.btn:focus,
.input:focus {
  outline: none;
}

.search.active .input {
  width: 300px;
  padding-bottom: 10px;
}

.search.active .btn {
  transition: all 0s;
  border: 0;
  background-color: rgba(179, 179, 179, 0);
}

.card {
  position: relative;
  border: 4px groove #5aa46a;
  border-radius: 10px;
  height: 20vw;
  width: 37vw;
  max-height: 150px;
  max-width: 500px;
  margin: 10px 0px;
  display: grid;
  grid-template-columns: 2fr 4fr 1fr;
  grid-template-rows: 1fr;
  grid-template-areas: '. . .';
  direction: ltr;
  background-color: #c0c0c0;
  transition: all 0.3s ease;
  cursor: pointer;
}

.img {
  height: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-width: 7vw;
  border-radius: 5% 0 0 5%;
}

.info-wrapper {
  margin-left: 10px;
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
}

.name-wrapper {
  display: flex;
  justify-content: space-between;
  position: relative;
  align-items: flex-start;
  max-height: min(15vw, 70px);
  overflow: hidden;
}

.card-name {
  font-size: min(1.8vw, 20px);
  margin-right: 5%;
}

.rating-wrapper {
  padding: 2%;
  border-radius: 30%;
  border: 1px solid rgba(0, 0, 0, 0.6);
}

.rating {
  font-size: min(2vw, 25px);
  color: white;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000,
    1px 1px 0 #000;
}

.genre-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.genre {
  background-color: rgb(151, 148, 148);
  border: 1px solid rgba(0, 0, 0, 0.157);
  color: rgb(1, 255, 1);
  display: inline-block;
  padding: 1.5%;
  border-radius: 10px;
  text-align: center;
  font-size: min(1.4vw, 14px);
  text-shadow: -1px -1px 0 rgba(0, 0, 0, 0.5), 1px -1px 0 rgba(0, 0, 0, 0.5),
    -1px 1px 0 rgba(0, 0, 0, 0.5), 1px 1px 0 rgba(0, 0, 0, 0.5);
  white-space: nowrap;
}

.dates-container {
  display: flex;
  justify-content: space-between;
}

.date {
  font-size: min(1.3vw, 14px);
  display: flex;
  flex-direction: column;
  white-space: nowrap;
}

.date-divider {
  height: 100%;
  width: 2px;
  background-color: rgba(0, 0, 0, 0.253);
  margin: 0px 3px;
}

.arrow-container {
  display: flex;
  align-items: center;
  margin-left: auto;
  font-size: min(10vw, 120px);
  padding-left: 10%;
  color: transparent;
  background: #666666;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
  text-shadow: 0px 3px 3px rgba(190, 238, 173, 0.5),
    0px 3px 3px rgba(190, 238, 173, 0.5);
  transition: all 0.3s ease;
}

.card.active > .arrow-container {
  -webkit-transform: translateX(50%) scale(130%);
          transform: translateX(50%) scale(130%);
  text-shadow: 0 0px 4px rgb(136, 255, 0), -2px 5px 2px #474747;
}

.card.active {
  position: -webkit-sticky;
  position: sticky;
  top: 5px;
  bottom: 5px;
  z-index: 5;
  background-color: #86d998;
  -webkit-transform: scale(103%);
          transform: scale(103%);
  box-shadow: 0px 11px 8px -5px rgba(0, 0, 0, 0.49);
}

.green {
  background-color: rgb(11, 187, 11);
}

.orange {
  background-color: orange;
}

.red {
  background-color: red;
}

.black {
  background-color: black;
}

.info-container {
  direction: ltr;
  display: flex;
  flex-direction: column;
  position: -webkit-sticky;
  position: sticky;
  top: 10px;
  width: 100%;
  border: 1px solid black;
  border-radius: 5px 5px 0 0;
}

.banner-img {
  position: relative;
  width: 100%;
}

.info-name {
  font-size: min(3vw, 30px);
  color: var(--primary-font-color);
  background-color: var(--primary-back-color);
  padding: 1% 2%;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000,
    2px 2px 0 #000;
}

.summary {
  padding: 1% 3%;
}

.summary-title {
  font-size: min(2vw, 20px);
}

.mark {
  color: var(--primary-font-color);
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000,
    1px 1px 0 #000;
}

.text-container {
  position: relative;
  direction: rtl;
  height: 8vw;
  overflow-y: auto;
  padding: 0 2%;
  margin-top: 1%;
}

.summary-text {
  direction: ltr;
  font-size: min(1.5vw, 15px);
  text-align: justify;
  text-indent: 3%;
}

.video {
  margin-top: 15px;
  width: 100%;
  height: 20vw;
}

.placeholder-container {
  direction: ltr;
  display: flex;
  flex-direction: column;
  position: -webkit-sticky;
  position: sticky;
  top: 20vh;
  width: 100%;
  border: 3px solid black;
}

.placehodlder-text {
  text-align: center;
  background-color: black;
  color: white;
  font-size: min(2vw, 40px);
}

.placeholder-bg {
  width: 100%;
}

.container {
  width: 100vw;
  height: calc(100vh - 80px);
  display: flex;
  position: relative;
  background-image: url(/static/media/backwalpp.3db3643b.jpg);
  background-position: center;
  background-size: contain;
  justify-content: center;
}

.container::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(83, 79, 79, 0.397);
}

.listing-wrapper {
  position: relative;
  background-color: rgb(223, 219, 219);
  width: 80%;
  max-width: 1300px;
  z-index: 1;
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-template-rows: 1fr;
  grid-column-gap: 2%;
  overflow-y: auto;
  overflow-x: hidden;
  direction: rtl;
  padding: 0 1%;
}

.cards-wrapper {
  width: 100%;
  max-width: 550px;
}

.card-info {
  width: 100%;
  height: 100%;
  position: relative;
}

:root {
  font-family: 'B612', sans-serif;
  --primary-back-color: #292626;
  --primary-font-color: #1cbd1c;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

::-webkit-scrollbar {
  width: min(1vw, 10px);
  height: min(1vw, 10px);
}
::-webkit-scrollbar-thumb {
  background: radial-gradient(circle, #1cbd1c 0%, #8cc28c 90%, #8cc28c 90%);
  border-radius: 1px;
}
::-webkit-scrollbar-track {
  background: #292222;
  box-shadow: inset 0px 0px 24px -2px #337028;
}

